.label {
  position: relative;
  margin-bottom: 20px;
  padding-left: 34px;
  font-size: 16px;
  line-height: 24px;
}
.label::before,
.label::after {
  position: absolute;
  content: "";
  border-radius: 50%;
}
.label::before {
  box-sizing: border-box;
  top: 0;
  left: 0;
  width: 24px;
  height: 24px;
  background-color: #FFFFFF;
  border: 2px solid #BDBDBD;
}
.label::after {
  display: none;
  top: 12px;
  margin-top: -8px;
  left: 4px;
  width: 16px;
  height: 16px;
  background-color: #7859CF;
}
.label:hover::before,
.label:focus::before {
  border-color: #9070EC;
  outline: transparent;
}
.label:hover::after,
.label:focus::after {
  background-color: rgba(120, 89, 207, 0.25);
}
.label--semibold {
  font-weight: 600;
}
.label--for-checkbox {
  padding-left: 52px;
}
.label--for-checkbox::before {
  width: 42px;
  background-color: #BDBDBD;
  border: none;
  border-radius: 12px;
}
.label--for-checkbox::after {
  display: block;
  top: 10px;
  left: 2px;
  width: 20px;
  height: 20px;
  background-color: #FFFFFF;
}
.label--for-checkbox:hover::before,
.label--for-checkbox:focus::before {
  background-color: rgba(120, 89, 207, 0.25);
  border: 2px solid #9070EC;
  outline: transparent;
}
.label--for-checkbox:hover::after,
.label--for-checkbox:focus::after {
  background-color: #FFFFFF;
  outline: transparent;
}
.label--for-checkbox:last-child {
  margin-bottom: 0;
}
.label--for-radio:last-child {
  margin-bottom: 0;
}
